body {
    --primary-hue:200;
    --primary-sat:94%;
    --primary-lum:57%;
    --secondary-hue:0;
    --secondary-sat:0%;
    --secondary-lum:100%;
    --background-hue:0;
    --background-sat:0%;
    --background-lum:0%;
    --shadow: 0 0 10px rgba(0,0,0,0.5);
    --background-img: url(/rw-cart/static/media/background.4a077d6d.jpeg);
}
/* ################## top-level styling ################## */
html, body, #root {
  background-color: hsl(var(--background-hue), var(--background-sat), calc(var(--background-lum) - 5%));
  /* background-image: var(--background-img); */
  /* background-image: url("./images/background.jpeg"); */
}


.App {
  display: flex;
  flex-flow: column;
  height: 100%;
  width: 100%;
  position: absolute;
  left: 0px;
  top: 0px;
}

header {
