}

.app-space {
  display: flex;
  flex: 1 1;
  padding: 1em 0;
  overflow: hidden;
  position: relative;
}

.body-margin {
  flex: 1 2 100px;
}

.body-main {
  flex: 1 1 1200px;
  display: flex;
}

.column {
  
}
.column:not(:last-child) {
  margin-right: 1em;
}

.column-header {
  display: flex;
