.column {
  display:flex;
  flex-direction:column;
  flex: 1 1;
  overflow: hidden;
}
.column.center{
  flex: 1 1
}
.column.left {
  flex: 0 1 300px;
}

.column.right .panel-buttons {
  border-top: 1px solid var(--secondary-contrast);
}

.column-segment {
  display: flex;
  justify-content: space-between;
  border-top: 1px solid;
  width: auto;
}
.column-segment.padded {
  padding: 30px;

}

.super-label {
  font-size: 1em;
  /* text-align:center; */
  font-family: 'Barlow Condensed', sans-serif;
}

.column.left .column-segment {
  border-color: var(--primary-contrast);
}
.column.right .column-segment {
  border-color: var(--secondary-contrast);
}

.text-button {
  cursor: pointer;
  padding: 16px;
  display: flex;
  justify-content:center;
  --button-hue: var(--primary-hue);
  --button-sat: var(--primary-sat);
  --button-lum: var(--primary-lum);
  --button-text-color: var(--primary-contrast);
  background-color: hsl(var(--button-hue), var(--button-sat), var(--button-lum));
  color: var(--button-text-color);
}
.text-button:hover {
  background-color: hsl(var(--button-hue), var(--button-sat), calc(var(--button-lum) + 15%));
}
.text-button.dim {
  --button-lum: 30%;
}
.text-button.disabled {
  cursor: not-allowed;
  --button-sat: 0%;
}
.text-button.secondary {
  --button-hue: var(--secondary-hue);
  --button-sat: var(--secondary-sat);
  --button-lum: var(--secondary-lum);
  --button-text-color: var(--secondary-contrast);
}

.text-button ~ .text-button {
  border-left: 1px solid white;
}


/* ################## modal styling ################## */



.modal {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-anchor {
  position: absolute;
  left: 50%;
  top: 50%;
}

.modal-content {
  position: relative;
  left: -50%;
  top: -50%;
}

.draggable .modal-header {
  cursor:all-scroll;
}

.modal.abs {
  top: 0;
  left: 0;
  position: absolute;
  z-index: 0;
}

.modal-layer {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  z-index: 100;
}

.modal-backdrop {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: hsla(0,0%,var(--background-max),0.5);
}

.modal-body {
  display: flex;
  flex-wrap: wrap;
  padding: 10px;
  justify-content:center;
}

.modal-container {
  overflow: hidden;
  border-radius: 7px;
  background-color: hsl(var(--background-hue), var(--background-sat), var(--background-lum));
  color: var(--background-contrast);
  z-index: 100;
  box-shadow: var(--shadow);
}

.modal-container .panel-buttons {
  background-color: var(--brand-dark);
}

.modal-header {
  font-size: 1.25em;
  padding: 1em;
  background-color: hsl(var(--primary-hue), var(--primary-sat), var(--primary-lum));
  color: var(--primary-contrast);
}

#submitQuote .input-set {
  width: 200px;
}

.input-set {
  flex: 1 1;
  display: flex;
  flex-flow:column;
  margin: 5px;
}
.input-set-label {
  font-family: 'Barlow Condensed', sans-serif;
}

.input-set > input, .input-set > textarea {
  flex: 1 1;
  border: none;
  background-color: hsla(0,0%,var(--background-max),0.1);
  padding: 0.5em;
  border-radius:3px;
  color: var(--background-contrast);
}

.input-set.textarea {
  flex: 2 1;
}

input.fault {
  border: 1px solid red;
}

.colorz-anchor {
  position: absolute;
  bottom: 10px;
  left: 10px;
  height: 50px;
  width: 1px;
}

.colorz-anchor .modal-container {
  position: relative;
  top: -250px;
  width: 250px;
}

.colorz-icon {
  display: flex;
  justify-content:center;
  align-items:center;
  width: 50px;
  height: 50px;
  border-radius: 25px;
  color: var(--primary-contrast);
  font-size: 2em;
  cursor: pointer;
  box-shadow: var(--shadow);
  background-image: linear-gradient(to right, hsl(var(--primary-hue),var(--primary-sat),var(--primary-lum)),
                                             hsl(var(--secondary-hue),var(--secondary-sat),var(--secondary-lum)));
}

.pic-preview {
  max-height: 500px;
  margin:100px;
  display:flex;
  flex-direction:column;
}

.pic-preview > .modal-header {
  flex: 0 1;
}

.pic-preview .modal-body {
  overflow:hidden;
}
.pic-preview img {
  object-fit: contain;
}

.pic {
  height:100%;
  width:100%;
}


/* ################## navigation styling ################## */

nav {
  display: flex;
  position: -webkit-sticky;
  position: sticky;
}

nav ul {
  justify-content: space-around;
  height: 100%;
}

nav li {
  flex: 1 1;
  font-size: 18px;
  padding: 0.75em;
  text-align: center;
}
nav li:hover{
  background-color: white;
  cursor: pointer;
}

nav .margin-left, nav .margin-right {
  display: flex;
  flex: 1 0 10em;
  justify-content: center;
}

nav .main-nav {
  flex: 4 0 10em;
}

.main-nav ul {
  color: var(--primary-contrast);
}
.main-nav li:hover {
  color: hsl(var(--primary-hue), var(--primary-sat), var(--primary-lum));
