/* ################## selector styling ################## */

.selector-set {
  width: 100%;
  /* border-left: 1px solid; */
}

.selector-set.empty, .selector-set.waiting {
  display: none;
}

.selector-set > .super-label {
  /* display: none; */
}


/* ################## colorz styling ################## */

.color-node {
  width: 200px;
  padding: 5px;
}

.color-node > label {
  margin-left: 10px;
}


/* ################## radio button styling ################## */

.radio-list{
  display: flex;
  justify-content:space-between;
  width: 100%;
}

.radio-option {
  flex: 1 1;
  display: flex;
  justify-content: center;
  cursor: pointer;
  padding: 5px 6px;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  border-radius: 7px;
  align-items: center;
}
.radio-list .checked {
  font-weight: 700;
  border: 1px solid;
}

.radio-set {
  /* width: 100%; */
  flex: 1 1;
}

.radio-set > .super-label {
  display: none;
}


/* ################## date picker styling ################## */

.date-selector {
  display: flex;
  flex-flow: column;
}

.date-selector .super-label {
  text-align: left;
}

.date-selector input {
  background-color: transparent;
  font-size: 1em;
  text-align: left;
  border: none;
  cursor: pointer;
  padding: 0.5em;
  width: 6em
}
.date-selector input:hover {
  background-color: rgba(255,255,255,0.25);
}
.date-selector input:focus {
  background-color: rgba(0,0,0,0.2);
  outline: none;
  cursor: text;
}

.date-selector.white input {
  color: white;
}
.date-selector.black input{
  color: black;
}

.react-datepicker-wrapper input {
  padding: 0;
}


/* ################## loader/blip styling ################## */

@keyframes pulse {
  0%, 50% {
    transform: scale(0.3);
  }
  25% {
    transform: scale(1);
  }
  100% {
    transform: scale(0.3);
  }
}

.loader-text {
  text-align:center;
  font-family: 'Barlow Condensed', sans-serif;
  letter-spacing: 3px;
  font-size: 1em;
}

.loader-blip {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
}

.blips {

}

.blip {
  animation: pulse 1s infinite;
  animation-timing-function: ease-out;
  background-color: var(--background-contrast);
  border-radius: 50px;
  height: 10px;
  width: 10px;
}

.blip:nth-child(2){
  animation-delay: 0.1s
}
.blip:nth-child(3){
  animation-delay: 0.2s
}
.blip:nth-child(4){
  animation-delay: 0.3s
}
.blip:nth-child(5){
  animation-delay: 0.4s
}
.blip:nth-child(6){
  animation-delay: 0.5s
}
.blip:nth-child(7){
  animation-delay: 0.6s
}
