}
.row-expander.btn.expanded {
    border-bottom-right-radius: 7px;
}

.catalog-item-elements {
    flex: 1 1;
}

.container-header, .catalog-item {
    display: flex;
    flex: 1 1;
}

.row-expander {
    display: flex;
    align-items: center;
    min-width: 16px;
    min-height: 16px;
}

.row-expander.btn:hover {
    background-color: hsl(var(--primary-hue), var(--primary-sat), calc(var(--primary-lum) + 5%));
}

.catalog-item-elements li {
    align-items: center;
}

.catalog-item-elements .item-image > * {
    border-radius: 7px;
    overflow: hidden;
    border: 1px solid hsla(var(--secondary-hue), var(--secondary-sat),var(--secondary-lum), 0.7);
}

.dbw-table .catalog-item-elements li:nth-child(2) > * {
