/* ##### grid ##### */

.dbw-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    overflow: scroll;
}

.dbw-grid::-webkit-scrollbar {
    display:none;
}

.dbw-grid .catalog-item-container {
    min-height: 350px;
}

.dbw-grid .catalog-item-container.expanded {
    grid-row: span 2;
}

.dbw-grid .catalog-item, .dbw-grid .container-header {
    flex-flow: column;
}

.dbw-grid .catalog-item-elements {
    display: flex;
    flex-flow: column;
    align-items: center;
    padding: 0.5em;
}

.dbw-grid li {
    margin-bottom: 5px;
}

.dbw-grid .item-description {
    order: 1;
    height: 2em;
    align-items:flex-start;
}

.dbw-grid .item-image {
    order: 2;
    width: 90%;
    overflow: hidden;
    display: flex;
    justify-content: center;
}

.dbw-grid .item-quantity {
    order: 3;
}

.dbw-grid .item-rate {
    order: 4;
}

.item-segment .grid-label {
    margin-right:1em;
}

.catalog .node-detail {
    border-color: hsla(0, 0%, var(--background-max), 0.15);
}

/* ##### table ##### */

.dbw-table {
    display: flex;
    flex: 1 1;
    flex-direction: column;
    overflow: hidden;
    width: 100%;
}

.dbw-table .grid-label {
    display: none;
}

.dbw-table ul {
    margin-bottom: 2px;
}

.dbw-thead {
    font-family: "Barlow Condensed", sans-serif;
    font-size: 1.25em;
}
.dbw-thead li {
    /* border-bottom set by component */
}

.dbw-tbody {
    overflow-y: scroll;
}
.dbw-tbody::-webkit-scrollbar {
    display: none;
}

.dbw-table .item-description {
    order: 3;
    flex:1 1;
}

/* ------------- */

.catalog .node-label {
    order: 1;
}

.catalog .node-label, .catalog .node-button {
    padding: 0;
}

.catalog .node-button{
    /* background-color: hsl(var(--primary-hue), var(--primary-sat), var(--primary-lum)); */
}

.row-expander.hide {
    display: none;
}

.catalog .node-children {
    margin-left: 16px;
    background-color: hsla(0, 0%, var(--primary-max), 0.1);
}

.catalog .node-button {
    border-radius: 5px 0 0 5px;
    overflow: hidden;
}

.address-chain {
    display: flex;
    font-family: 'Barlow Condensed', sans-serif;
}
.chain-link {
    display: flex;
}
.link-spacer {
    margin: 0 10px;
}
.link-label:hover {
    cursor: pointer;
    text-decoration:underline;
}
  
  /* .dbw-table .item-segment:nth-child(2) - see table/grid general section */
  
.dbw-table .item-quantity {
    order: 1;
    width: 100px
}
.dbw-table .item-rate {
    order: 2;
    width: 75px;
}
.dbw-table .item-image {
    order: 3;
    width: 200px;
    display: flex;
    justify-content: center;
  
}
.dbw-table .item-image-thumbnail {
    width: auto;
}
.dbw-table .item-image-thumbnail > img {
    width: -webkit-fill-available;
}
  
.dbw-table .catalog-item-elements {
    height: 200px;
    overflow: hidden;
    border-radius: 7px;
}
  
.collapsible-container {
    flex: 1 1;
    transition: height 0.2s ease;
}

.container-accessories {
    margin-left: 1em;
}
.page-control {
    border-top: 1px solid var(--background-contrast);
    padding: 5px;
    display: flex;
    justify-content:center;
}

.page-button {
    cursor: pointer;
    margin: 0 2px;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 20px;
    width: 20px;
}
.page-button.current-page{
    background-color: var(--background-contrast);
    color: hsl(var(--background-hue), var(--background-sat), var(--background-lum));
    border-radius: 50%;
}
.page-button.disabled {
    opacity: 0.5;
    cursor: not-allowed;
}
/* ################## cart general ################## */

.cart {
    display: flex;
    flex-flow: column;
    flex: 1 1;
    overflow: hidden;
    color: var(--secondary-contrast);
}

.cart-node {
    padding: 3px;
}

.cart-contents {
    overflow:hidden;
    flex: 1 1;
}

.cart-content .dbw-tbody {
    flex: 1 1;
    overflow-y: scroll;
}

.cart .bullet {
    
}
.cart .bullet.hide {
    display: none;
}

.quantity-static {
    text-align: center;
}

.cart .super-input-container {
    background-color: hsl(0, 0%, var(--secondary-min));
    border-radius: 3px;
}

.cart .node-detail .super-input-container {
    cursor: text;
}


/* ################## cart expander ################## */

.cart .node-label {
    order: 1;
}

.cart .node-children {
    margin-left: 16px;
    background-color: hsla(0, 0%, var(--secondary-max), 0.05);
}

.cart .KIT ~ .node-children, .cart .COMPLETE ~ .node-children {
    border-left: 1px solid var(--secondary-contrast);

}

.cart .TIER {
    font-family: 'Barlow Condensed', sans-serif;
    font-size: 22px;
    border-bottom: 1px solid var(--secondary-contrast);
}

.cart .TIER .quantity{
    display: none;
}


/* ################## cart columns ################## */

.cart-col {
    white-space: nowrap;
    text-overflow:ellipsis;
    overflow: hidden;
    padding-left: 5px;
}

.cart-col:not(:last-child) {
    margin-right: 2px;
}

.cart-item > ul, .collapser-content > ul{
    flex: 1 1;
}

.cart-col.spacer {
    width: 15px;
}

.cart-col.quantity {
    width: 30px;
    text-align:center;
}

.cart-col.name {
    flex: 1 1;
    display: flex;
    cursor: context-menu;
}

.cart-col.name > div {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    flex: 1 1;
}
.cart-col.name > i {
    width: 1em;
}

.cart-col.rate {
    width: 100px;
    padding-right: 10px;
}

.cart-col.numeric {
    text-align: right;
}


/* ################## cart summary ################## */

.cart-summary {
    flex-flow: column;
    background-color: hsl(0, 100%, var(--background-max));
    color: hsl(0, 100%, var(--background-min));
    font-size: 20px;
}

.cart-summary-row {
    flex: 1 1;
    display: flex;
    justify-content: space-between;
}
.cart-summary-row ~ * {
    margin-top: 10px;
}

.grand {
    font-size: 1.5em;
}

.cart .dbw-thead li {
    border-bottom: 1px solid var(--secondary-contrast);
}

.panel-buttons {
    display: flex;
}
.panel-buttons > * {
    flex: 1 1;
}


/* ################## slide panel ################## */

.slide-panel {
    --test: 50;
    position: absolute;
    display: flex;
    flex-flow: column;
    transition: right 0.2s ease;
    height: calc(100% - 2em);
    width: 400px;
    box-shadow: 0 0 10px rgba(0,0,0,0.5);
    border-radius: 7px 0 0 7px;
    background-color: hsl(var(--secondary-hue), var(--secondary-sat), var(--secondary-lum));
}

.slide-panel.in {
    right: 0%;
}
.slide-panel.out {
    right: -100%;
}

.slide-panel-switch{
    position: absolute;
    display: flex;
    top: 1px;
    right: 0px;
    padding: 0.25em;
    z-index: 2;
    background-color: hsl(var(--secondary-hue), var(--secondary-sat), var(--secondary-lum));
    color: var(--secondary-contrast);
    border-radius: 7px 0 0 7px;
    cursor: pointer;
    margin-top: 1.75em;
    font-size: 1.5em;
}

.slide-panel-switch i {
    padding: 3px 0.5em 0 0;
}
