@import url('https://fonts.googleapis.com/icon?family=Material+Icons');
@import url('https://fonts.googleapis.com/css2?family=Barlow+Condensed:wght@300;400&display=swap');

html, body, #root {
  height: 100%;
  width: 100%;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Oxygen', 'Roboto', 'Segoe UI', 
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif, -apple-system, BlinkMacSystemFont;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body::-webkit-scrollbar {
  display:none;
}

.barlow {
  font-family: 'Barlow Condensed', sans-serif;
  user-select: none;
}

.App {
  display: flex;
  flex-flow: column;
}

input::placeholder {
  color: rgba(255, 255, 255, 1);
  font-style: italic;
}

h1 {
  display: flex;
}

i {
  font-family: 'Material Icons';
  font-style: normal;
  font-weight: normal;
  font-size: 1em;
  user-select: none;
}

i ~ span, i ~ div, i ~ ul {
  margin-left: 0.25em;
}

.display-flex {
  display: flex;
}

.flex-1 {
  flex: 1;
}

.flex-0 {
  flex: 0;
}

.btn {
  cursor: pointer;
  
}

.btn.inactive {
  cursor: not-allowed;
  color: rgba(200, 200, 200, 0.5);
}

i.btn.large {
  font-size: 2em;
}

ul {
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0;
}

input:focus, textarea:focus {
  outline: none;
}

.numeric {
  text-align: right;
}